export default function skillsMobile() {
    return (
        <div className="SK--container">
            <div className="SK--divider">
                <Grid container>
                    <Grid item xs>
                        <hr className="SK--divider__bar" />
                    </Grid>
                    <Grid item xs>
                        <p>My skills</p>
                    </Grid>
                    <Grid item xs>
                        <hr className="SK--divider__bar" />
                    </Grid>
                </Grid>
            </div>        

            <div className="SK--items">
                <Grid container justifyContent="center" spacing={3}>
                    {Skills.map(({ title, desc, techs }, i) => (
                        <Grid key={i.toString()} item>
                            <ScardMobile title={title} desc={desc} techs={techs}/>
                        </Grid>
                    ))}
                </Grid>                
            </div>
        </div>
    )
}
